:root{
  --header-main-color: #337AB7;
  --log-btn-color: #464646;

  --primary-color: #337AB7;
  --warning-color: #ecc633;
  --danger-color: #c32b66;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-header {
  background-color: #337AB7;
  background-color: var(--header-main-color);
  background: linear-gradient(180deg, rgba(51,122,183,1) 0%, rgba(22,70,111,1) 100%);
}
.btn-gray {
  color: #fff;
  background-color: var(--log-btn-color);
  border-color: var(--log-btn-color);
}

.btn-gray:hover {
  background-color: var(--header-main-color);
  border-color: var(--header-main-color);
}

.btn-primary {
  background-color: var(--header-main-color);
  border-color: var(--header-main-color);
}
.table tbody tr th {
  font-weight: 400;
}
.main_content {
  min-height: calc(100vh - 102px);
}
.box {
  width: 100%;
  padding: 30px 0;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  color: #f5f5f5;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  background-color: var(--header-main-color);
}

.box svg {
  font-size: 28px;
}
.btn-round {
  display: flex;
  width: 30px;
  height: 30px;
  padding: 0;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.btn-round svg{
  font-size: 18px;
}

.btn-outline-primary {
  color: var(--primary-color);
  border-color: var(--primary-color);
}

.btn-outline-primary:hover{
  background: var(--primary-color);
  background-color: var(--primary-color);
}

.btn-outline-warning {
  color: var(--warning-color);
  border-color: var(--warning-color);
}

.btn-outline-warning:hover{
  background: var(--warning-color);
  background-color: var(--warning-color);
}

.btn-outline-danger {
  color: var(--danger-color);
  border-color: var(--danger-color);
}

.btn-outline-danger:hover{
  background: var(--danger-color);
  background-color: var(--danger-color);
}
